3bee309", "topic": "When iterating over a map with a range loop, the iteration order is not specified and is not guaranteed to be the same from one iteration to the next. Since the release of Go 1.0, the runtime has randomized map iteration order. Programmers had begun to rely on the stable iteration order of early versions of Go, which varied between implementations, leading to portability bugs. If you require a stable iteration order you must maintain a separate data structure that specifies that order. This example uses a separate sorted slice of keys to print a map[int]string in key order:", "expanded": true}]}]}, {"id": "a4dc83dcfcbdd69f", "topic": "c", "expanded": false, "children": [{"id": "a4dc8483453b4025", "topic": "The C Programming Language <a href=\"https://item.jd.com/12580612.html\">", "expanded": true}, {"id": "a4dca8b9211acfb3", "topic": "SICP <a href=\"https://web.mit.edu/alexmv/6.037/sicp.pdf\">", "expanded": true}, {"id": "a4dd6cc83b6dbd55", "topic": "The Art of Computer Programming <a href=\"https://zh.wikipedia.org/wiki/%E8%AE%A1%E7%AE%97%E6%9C%BA%E7%A8%8B%E5%BA%8F%E8%AE%BE%E8%AE%A1%E8%89%BA%E6%9C%AF\">", "expanded": true}, {"id": "a4ddaac1ce288d26", "topic": "Niklaus Wirth\u2019s books", "expanded": true}, {"id": "a4dd979a174be569", "topic": "The Practice of Programming <a href=\"https://en.wikipedia.org/wiki/The_Practice_of_Programming\">", "expanded": true}]}]}, {"id": "98aa50649d58440c", "topic": "\u5b89\u5168\u5f00\u53d1", "expanded": false, "direction": "right", "children": [{"id": "a59094b92dfc5f06", "topic": "Venom", "expanded": true, "children": [{"id": "a5909894cb4b5ceb", "topic": "github.com/c-bata/go-prompt", "expanded": true}, {"id": "a590a1a432a57b71", "topic": "fmt.Sscanf(in, \"%s\", &cmdStr)", "expanded": true}]}]}, {"id": "a93c16fbbfb13ea3", "topic": "\u5f00\u53d1", "expanded": false, "direction": "right", "children": [{"id": "a93c19ed69afcf96", "topic": "\u524d\u7aef\u5f00\u53d1", "expanded": true, "children": [{"id": "a93c1bf